<template>
  <div>
    <div class="about"></div>
    <div class="top">
      <router-link to="/home" class="tops">返回首页</router-link>
      <router-link to="/register" class="tops">没有账号？去注册</router-link>
    </div>
    <div class="regis">
      <h1 class="title">二手图书交易网</h1>
      <!-- form表单 -->
      <el-form
        :model="loginForm"
        :rules="rules"
        ref="loginForm"
        label-width="100px"
        class="demoRegisterForm"
      >
      <!-- 学号 -->
        <el-form-item prop="studentCode" label-width="40px">
          <el-input
            prefix-icon="el-icon-reading"
            placeholder="学号"
            name="studentCode"
            v-model="loginForm.studentCode"
            onkeypress="return( /[\d]/.test(String.fromCharCode(event.keyCode) ) )"
          ></el-input>
        </el-form-item>
        <!-- 密码 -->
        <el-form-item prop="password" label-width="40px">
          <el-input prefix-icon="el-icon-lock" type="password" placeholder="密码" v-model="loginForm.password"></el-input>
        </el-form-item>
        <p class="bottom">
          登录即同意
          <a href>《二手书交易使用协议》</a>&
          <a href>《隐私权条》</a>
        </p>
        <el-form-item class="bot">
          <el-button type="primary" @click="submitForm('ruleForm')" class="bota">登录</el-button>
          <el-button @click="resetForm('ruleForm')" class="botb">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    // 验证学号
    const checkStudentCode = (rule, value, callback) => {
      let reg = /^[0-9]+$/;
      if (!value) {
        callback(new Error("学号不能为空"));
      } else if (value!="" && !reg.test(value)) {
        callback(new Error("学号必须为数字"));
      } else if (value.length!=8) {
        callback(new Error("学号必须是8位"));
      } else {
        callback();
      }
    };
    return {
      loginForm: {
        studentCode: "",
        password: ""
      },
      rules: {
        studentCode: [
          { required: true, validator: checkStudentCode, trigger: "blur" }
        ],
        password: [
          { required: true, message: "请输入用户密码", trigger: "blur" },
          { min: 6, max: 20, message: "密码长度在 6 到 20 个字符", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
      submitForm(){
          const studentCode = this.loginForm.studentCode
          const password = this.loginForm.password
          let param = new FormData()
          param.append('studentCode',studentCode)
          param.append('password',password)
        this.$refs.loginForm.validate(async valid=>{
          if(!valid) return;
          this.$http({
            url: "/api/login",
            method: "post",
            crossdomain: true,
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded',
            },
            data: param
            }).then(res => {
                console.log(res)
                if(res.data.status === -6)
                      return this.$message.error('用户名或密码错误')
                this.$message.success('登录成功')
                console.log(res.data.data)
                window.sessionStorage.setItem('token',res.data.data)
                this.$router.push('/home')
            }).catch(res =>{
                console.log('error')
            })
        })
      },
      resetForm(formName){
          this.$refs[ruleForm].resetFields();
      }
  }
}
</script>
<style scoped >
.about {
  position: relative;
  width: 100%;
  height: 570px;
  background: url("../assets/register.jpg");
  z-index: -1;
  background-attachment: fixed;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
}
.regis {
  width: 380px;
  height: 350px;
  background-color: rgba(216, 214, 214, 0.573);
  box-shadow: 0 0 10px rgb(95, 94, 94);
  border-radius: 5px;
  position: absolute;
  left: 50%;
  top: 43%;
  transform: translate(-50%, -43%);
}
.title {
  text-align: center;
  color: ivory;
}
.demoRegisterForm {
  width: 90%;
}
.bottom {
  color:rgb(54, 54, 53);
  font-size: 13px;
  text-align: center;
  width:100%;
  margin: 27px 22px 20px;
}
.bottom a {
  color: rgb(185, 42, 9);
  text-decoration: none;
}
.top{
  position: absolute;
  top: 14%;
  left: 35%;
  width:380px;
  display: flex;
  justify-content: space-between;
}
.top .tops{
  text-decoration: none;
  color:rgb(92, 91, 90);
  font-size: 12px;
  font-weight: 530;
}
.bot{
    /* border:1px red solid; */
    margin-top:30px;
}
.el-button .botb .el-button--default{
    margin-left: 30px;
}
</style>