        <div class="navigat">
          <ul>
            <li><router-link to="/personal">我的主页</router-link></li> 
            <li><router-link to="/shopcar">购物车</router-link></li>    
            <li ><router-link :to="token?'/kefu': '/login'">{{token}}</router-link></li>   
            <li><div @click="logout">退出</div></li>      
          </ul>
        </div>
      </div>
      <!-- 中间区域 -->
      <div class="oldtitle">
        <div class="h1">
          <h1>二手书交易网</h1>
        </div>
        <div class="navtitle">
          <ul>
           <router-link to="/home">首页</router-link>
            <router-link to="/sell">出售</router-link>
            <router-link to="/orderList">我的订单</router-link>
            <router-link to="/fabued">我的发布</router-link>
          </ul>
        </div>
      </div>
      <!-- 主页中心 -->
      <div class="buymain">
        <div class="mianbuy">
          <div class="mainleft">
            <!-- <img :src="'http://49.234.70.67:8081/'+authorsList.headSrc" /> -->
            <div class="proBook">
              <h3 style="margin-bottom:10px">简介</h3>
              <p>姓名: {{authorsList.userName}}</p>
              <p>学号: {{authorsList.studentCode}}</p>
              <p>电话: {{authorsList.phoneNumber}}</p>
            </div>
          </div>
          <div class="mainright">
            <div class="booktop">
              <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item>
                  <b>{{authorsList.userName}}</b>的主页
                </el-breadcrumb-item>
                <el-breadcrumb-item>Ta的二手书</el-breadcrumb-item>
                <el-breadcrumb-item>计算机网络</el-breadcrumb-item>
              </el-breadcrumb>
            </div>
            <!-- 书籍中心 -->
            <div class="bookdes">
              <div>
                  <el-image
                    style="width: 150px; height: 200px"
                    :src="'http://49.234.70.67:8081/'+this.bookSrc">
                  </el-image>
              </div>
              <div class="contentbook">
                <p class="booksname">{{this.bookName}}</p>
                <p>类型: {{this.bookStyle.join('/')}}</p>
                <p>价格: {{this.price}} 元</p>
                <p>库存: {{this.stock}}本</p>
                <p>访问量: {{this.bookCount}} 次</p>
                <p>上架时间: {{this.bookDate|dataFormat}}</p>
              </div>
            </div>
            <div class="bookIntroduction" style="padding:10px">
              <p>
                {{this.bookIntroduction}}
              </p>
            </div>
            <div class="buybook">
              <b @click="toBuy">购买</b>
