                :src="squireList"
              ></el-avatar>
            </div>
            <div class="liuyanbottom">
              <p>羽轩 ♀ / 04182138</p>
              <p>我的留言.......</p>
              <p>
                2020-08-10 22:26:39
                <span>回复</span>&nbsp;&nbsp;&nbsp;
