    <footer-box></footer-box>
  </div>
</template>
<script>
import footer from '../components/footer.vue'
export default {
  data(){
    return{
      token:'客服',
      // 作者信息
      authorsList:[],
      // 书籍信息
      booksList:[],
      // 头像
      circleList:'',
      bookImg:'http://cdn.shujie.me/isbn/532/770/9787532770243_05.jpg!w600',
      squireList:'',
      bookCount:0,//书籍的访问量
      bookId:this.$route.query.bookId,
      bookSrc:this.$route.query.bookSrc,
      bookName:this.$route.query.bookName,
      price:this.$route.query.price,
      bookStyle:this.$route.query.bookStyle,
      stock:this.$route.query.stock,
      bookIntroduction:this.$route.query.bookIntroduction,
      studentCode:this.$route.query.studentCode,
      userName:this.$route.query.userName,
      bookDate:this.$route.query.bookDate
    }
  },
  components:{
        "footer-box":footer
  },
  mounted(){
      // 获取作者信息以及  获取书籍信息
      // 获取作者头像  昵称 学号 手机号
      this.getAuthorsInfo()
      // 获取书籍 名称 图片  类型  价格  库存  （访问量） 简介  
      // 获取书籍的访问量
      this.getBookCount()
  },
  methods:{
    // 获取书籍的访问量
    getBookCount(){
        this.$http({
            url: `http://49.234.70.67:8081/u/count/${this.bookId}`,
            method: "get",
            crossdomain: true,
            headers: {
                'Content-Type': 'application/json',
                'token':window.sessionStorage.getItem('token')
            },
            }).then(res => {
              // console.log('ok',res)
              // console.log(res.config.headers.token)
              if(!res.config.headers.token){
                this.token = '登陆/注册'
              } 
              if (res.data.status === 200) {
                  this.bookCount = res.data.data[0].count
              }
              if (res.data.status === -1) {
                  this.$message.error(res.data.message);
                  this.$router.push('/login')
              }
            }).catch(res=>{
              console.log('未获取到')
            })
    },
    //  购买
    // 添加商品到购物车
    toBuy(){
        const shopCarList = {
              bookId:this.bookId,
              bookSrc:this.bookSrc,
              bookName:this.bookName,
              sellerStudentCode:this.studentCode,
              sellerName:this.userName,
              price:this.price
        }
        this.$http({
            url: "http://49.234.70.67:8081/u/add/buyCar",
            method: "post",
            crossdomain: true,
            headers: {
                'Content-Type': 'application/json',
                'token':window.sessionStorage.getItem('token')
            },
            data:shopCarList
            }).then(res => {
              console.log(res)
              this.$router.push('/shopcar')
              if (res.data.status === -1) {
                  this.$message.error(res.data.message);
                  this.$router.push('/login')
              }
            }).catch(res=>{
              console.log('未获取到')
            })
    },
    // 作者
    getAuthorsInfo(){
      this.$http({
            url: "http://49.234.70.67:8081/u/home",
            method: "get",
            crossdomain: true,
            headers: {
                'token':window.sessionStorage.getItem('token')
            }
            }).then(res => {
              console.log(res)
              this.authorsList = res.data.data[0]
              if (res.data.status === -1) {
                  this.$message.error(res.data.message);
                  this.$router.push('/login')
              }
            }).catch(res=>{
              console.log('未获取到')
            })
    },
     // 退出
    logout(){
        this.$http({
            url: "http://49.234.70.67:8081/u/logout",
            method: "get",
            crossdomain: true,
            headers: {
                'token':window.sessionStorage.getItem('token')
            }
            }).then(res => {
              console.log(res)
              window.sessionStorage.clear();
              this.$router.push("/login");
              if (res.data.status === -1) {
                  this.$message.error(res.data.message);
                  this.$router.push('/login')
              }
            }).catch(res=>{
              console.log('未获取到')
            })
      },
  }
};
</script>
