import Vue from 'vue'
// 全部导入element ui
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import App from './App.vue'
import router from './router'
import store from './store'
// import qs from 'qs'
// 导入home组件的样式
import './assets/css/home.css'


Vue.use(ElementUI);
// 导入axios
import axios from 'axios'

// 配置请求根路径 
// Vue.prototype.$http.defaults.baseURL =''
// axios.defaults.baseURL = '/a'

axios.defaults.withCredentials = true; 　
//=>POST请求
// axios.defaults.headers.post['content-type']='application/json'
axios.defaults.headers = {'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'};
// axios.defaults.headers.post['Content-Type'] = 'application/json';
// axios.defaults.transformRequest = [function (data) {
//   let src = ''
//   for (let item in data) {
//     src += encodeURIComponent(item) + '=' + encodeURIComponent(data[item]) + '&'
//   }
//   return src
// }]
//时间过滤器
Vue.filter('dataFormat',function(originalValue){
  const dt = new Date(originalValue)
  const y = dt.getFullYear()
  const m = (dt.getMonth()+ 1 +'').padStart(2,'0')
  const d = (dt.getDay() +'').padStart(2,'0')

  // const hh = (dt.getHours() +'').padStart(2,'0')
  // const mm = (dt.getMinutes() +'').padStart(2,'0')
  // const ss = (dt.getSeconds() +'').padStart(2,'0')
  return `${y}-${m}-${d}`
})
// 请求拦截器
axios.interceptors.request.use(config=>{
  config.headers.Authorization = window.sessionStorage.getItem('token')
  // let token = localStorage.getItem('token')
  // token &&(config.headers.Authorization=token)
  return config
},error=>{
  return Promise.reject(error)
})
// 响应拦截器异常处理
axios.interceptors.response.use(response => {
  return response
},error=>{
  // console.log(error)
  if(error&&error.response){
    switch(error.response.status){
      //=>错误状态处理
      case 400:
        this.$message.error('手机号码格式错误')
        break;
      case -4:
       
        this.$message.error('短信验证码错误')
        break;
      case -5:
       
        this.$message.error('该学号被注册过了')
        break;
      case -3:
       
        this.$message.error('教务系统验证错误')
        break;
      case -1:
       
        this.$message.error('页面长时间未登录，请重新登录')
        break;
    }
  }else{
    if(!window.navigator.onLine){
      //=>断网处理
      return 
    }
  }
})
// Vue.prototype.$qs = qs
Vue.prototype.$http = axios
Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
