<script>
import footer from '../components/footer.vue'
export default {
  data(){
    return{
      item:'',
      total: 0,
      url:'',
      // 第一个书目列表
      recommendList: [],
      // 第二个书目列表
      hotBooksList:[],
      bookId:0,
      queryInfo: {
        message:'',//模糊查询的数据
        page: 1, //页数
        size:20 //当前页数的数据个数
      },
      token:'客服'
    }
  },
  mounted(){
    this.getAllBooks()
    // 模糊查询
    // this.getSerchBook()
    // if(this.token){
    //   this.$refs.regist.
    // }
  },
  components:{
        "footer-box":footer
  },
  methods:{
        // bindIcon(icon){
        //     // return require("http://49.234.70.67:8081/"+icon);
        //     return require('http://49.234.70.67:8081/')
        // },
      //获取所有书籍信息
      getAllBooks(){
        this.$http({
            url: "http://49.234.70.67:8081/u/search/books",
            method: "post",
            crossdomain: true,
            headers: { 
                'Content-Type': 'application/json',
                'token':window.sessionStorage.getItem('token')
            },
            data:{
                  page:this.queryInfo.page,
                  size:this.queryInfo.size
              }
            }).then(res => {
              console.log(res)
              // console.log(res.config.headers.token)
                if(!res.config.headers.token){
                  this.token = '登陆/注册'
                } 
                if(res.data.status === 200){
                this.recommendList = res.data.data[0]
                // conosle.log(this.recommendList)
                this.total =  res.data.data[1].total
                }
                if (res.data.status === -1) {
                  this.$message.error(res.data.message);
                  this.$router.push('/login')
                }
              console.log('我的书籍数据获取成功')
            }).catch(res=>{
              console.log('未获取到书籍')
            })
      },
      // 根据学号进行模糊查询
      getXueHaoBook(){
           this.$http({
            url: "http://49.234.70.67:8081/u/book/user",
            method: "post",
            crossdomain: true,
            headers: {
                'Content-Type': 'application/json',
                'token':window.sessionStorage.getItem('token')
            },
            data:this.queryInfo
            }).then(res => {
              console.log(res)
              if (res.data.status === 200) {
                  this.hotBooksList = res.data.data[0]
                  this.queryInfo.message = ''
                  // 暂且去掉total的赋值
                  // this.total = res.data.data[1]
              }
              if (res.data.status === -1) {
                  this.$message.error(res.data.message);
                  this.$router.push('/login')
              }
            }).catch(res=>{
              this.$message.error('未找到您想要的结果');
            })
      },
      // 搜索（书名的模糊查询）
      getSerchBook(){
          this.$http({
            url: "http://49.234.70.67:8081/u/search/some/book",
            method: "post",
            crossdomain: true,
            headers: {
                'Content-Type': 'application/json',
                'token':window.sessionStorage.getItem('token')
            },
            data:this.queryInfo
            }).then(res => {
              console.log(res)
              if (res.data.status === 200) {
                  this.hotBooksList = res.data.data[0]
                  this.queryInfo.message = ''
                  // 暂且去掉total的赋值
                  // this.total = res.data.data[1]
              }
              if (res.data.status === -1) {
                  this.$message.error(res.data.message);
                  this.$router.push('/login')
              }
            }).catch(res=>{
              console.log('未找到')
            })
      },
      // 退出
      logout(){
        this.$http({
            url: "http://49.234.70.67:8081/u/logout",
            method: "get",
            crossdomain: true,
            headers: {
                'token':window.sessionStorage.getItem('token')
            }
            }).then(res => {
              // console.log(res)
              window.sessionStorage.clear();
              this.$router.push("/login");
              if (res.data.status === -1) {
                  this.$message.error(res.data.message);
                  this.$router.push('/login')
              }
            }).catch(res=>{
              console.log('未获取到')
            })
      },
      // 分页
      handleSizeChange(newsize) {
        this.queryInfo.size = newsize;
        this.getAllBooks();
      },
      handleCurrentChange(newnum) {
        this.queryInfo.page = newnum;
        this.getAllBooks();
      }
  }
}
</script>
