    <div class="top">
      <router-link to="/home" class="tops">返回首页</router-link>
      <router-link to="/register" class="tops">没有账号？去注册</router-link>
    </div>
    <div class="regis">
      <h1 class="title">二手图书交易网</h1>
      <!-- form表单 -->
      <el-form
        :model="register2Form"
        :rules="rules"
        ref="register2Form"
        label-width="100px"
        class="demoRegisterForm"
      >
        <!-- 教务系统密码 -->
        <el-form-item prop="studentPassword" label-width="40px">
          <el-input prefix-icon="el-icon-lock" type="password" placeholder="教务系统密码" v-model="register2Form.studentPassword"></el-input>
        </el-form-item>
        <p class="bottom">
          注册即同意
          <a href>《二手书交易使用协议》</a>&
