        </p>
        <el-form-item class="bot">
          <el-button type="primary" @click="submitForm('register2Form')" >注册</el-button>
          <el-button @click="resetForm('ruleForm')" class="botb">上一步</el-button>
        </el-form-item>
      </el-form>
    </div>
