<script>
import qs from 'qs'
export default {
  data() {
    return {
      register2Form: {
        studentPassword:''
      },
      rules: {
        studentPassword: [
          { required: true, message: "请输入教务系统密码", trigger: "blur" },
          { min: 6, max: 20, message: "密码长度在 6 到 20 个字符", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
      submitForm(){
        const studentCode = this.$route.query.studentCode
        const userName = this.$route.query.userName
        const phoneNumber = this.$route.query.phoneNumber
        const smsCode = this.$route.query.smsCode
        const file = this.$route.query.file
        const password = this.$route.query.password
        const studentPassword = this.register2Form.studentPassword
        // console.log('学号：',studentCode)
        // console.log('昵称：',userName)
        // console.log('电话：',phoneNumber)
        // console.log('验证码：',smsCode)
        // console.log('头像：',file)
        // console.log('密码：',password)
        // console.log('教务系统：',this.register2Form.studentPassword)
        let param = new FormData()  // 创建form对象
        param.append('file', file)  // 通过append向form对象添加数据
        param.append('studentCode',studentCode) // 添加form表单中其他数据
        param.append('userName',userName)
        param.append('phoneNumber',phoneNumber)
        param.append('smsCode',smsCode)
        param.append('password',password)
        param.append('studentPassword',studentPassword)
        // console.log(param.get('file'))
        this.$refs.register2Form.validate(async valid=>{
        if(!valid) return;  
          this.$http({
            url: "/api/register",
            method: "post",
            crossdomain: true,
            headers: {
                'Content-Type': 'multipart/form-data' ,
                transformRequest: [data => data]
            },
            data: param
            }).then(res => {
              console.log(res)
                if (res.data.status === 400) {
                      return this.$message.error(res.data.message);
                  }else if (res.data.status === -4) {
                      return this.$message.error(res.data.message);
                  }else if (res.data.status === -3) {
                      return this.$message.error(res.data.message);
                  }else if (res.data.status === -5){
                      return this.$message.error(res.data.message);
                  }else{
                        this.$message.success('注册成功！')
                        this.$router.push('./login')
                  }
          }).catch(res => {
                  console.log(error.response.status)
          });
        })
      },
      resetForm(formName) {
        this.$router.push('./register')
      }
  }
}
</script>
