<template>
  <div>
    <div class="about"></div>
    <div class="top">
      <router-link to="/home" class="tops">返回首页</router-link>
      <router-link to="/login" class="tops">已有账号？去登录</router-link>
    </div>
    <div class="regis">
      <h1 class="title">二手图书交易网</h1>
      <!-- form表单 -->
      <el-form
        :model="registerForm"
        :rules="rules"
        ref="registerForm"
        label-width="100px"
        class="demoRegisterForm"
      >
      <!-- 学号 -->
        <el-form-item prop="studentCode" label-width="40px">
          <el-input
            prefix-icon="el-icon-reading"
            placeholder="学号"
            name="studentCode"
            v-model="registerForm.studentCode"
            onkeypress="return( /[\d]/.test(String.fromCharCode(event.keyCode) ) )"
          ></el-input>
        </el-form-item>
        <!-- 用户名 -->
        <el-form-item prop="userName" label-width="40px">
          <el-input prefix-icon="el-icon-user" placeholder="用户名" v-model="registerForm.userName">
            <i class="el-icon-user"></i>
          </el-input>
        </el-form-item>
        <!-- 手机号 -->
        <el-form-item prop="phoneNumber" label-width="40px">
          <el-input
            prefix-icon="el-icon-mobile-phone"
            placeholder="手机号"
            v-model.number="registerForm.phoneNumber"
          ></el-input>
        </el-form-item>
        <!-- 手机验证码 -->
        <el-row :gutter="10">
          <el-col :span="17">
            <el-form-item prop="smsCode" label-width="40px">
              <el-input
                prefix-icon="el-icon-postcard"
                placeholder="输入验证码"
                v-model="registerForm.smsCode"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="7">
            <!--发送验证码-->
            <el-button
              class="loginBtn codeBtn"
              type="primary"
              :disabled="isDisabled"
              @click="sendCode"
            >{{btntxt}}</el-button>
          </el-col>
        </el-row>
        <!-- 上传头像 -->
        <el-form-item label="上传头像" prop="file" :label-width="formLabelWidth">
          <el-upload
          class="avatar-uploader"
          action="https://www.mocky.io/v2/5185415ba171ea3a00704eed/posts/"
          :show-file-list="false"
          :on-change="handleAvatarSuccess"
          :before-upload="beforeAvatarUpload"
          :http-request="fileRequest">
          <img v-if="registerForm.file" :src="registerForm.file" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
      </el-upload>
    </el-form-item> 
        <!-- 密码 -->
        <el-form-item prop="password" label-width="40px">
          <el-input prefix-icon="el-icon-lock" type="password" placeholder="密码" v-model="registerForm.password"></el-input>
        </el-form-item>
        <el-form-item class="bot">
          <el-button type="primary" plain @click="submitForm('registerForm')">下一步</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>

export default {
  data() {
    // 验证学号
    const checkStudentCode = (rule, value, callback) => {
      let reg = /^[0-9]+$/;
      if (!value) {
        callback(new Error("学号不能为空"));
      } else if (value!="" && !reg.test(value)) {
        callback(new Error("学号必须为数字"));
      } else if (value.length!=8) {
        callback(new Error("学号必须是8位"));
      } else {
        callback();
      }
    };
    // 验证手机号
    var checkPhoneNumber = (rule, value, callback) => {
      const phoneReg = /^1[3|4|5|6|7|8][0-9]{9}$/;
      if (!value) {
        return callback(new Error("手机号码不能为空"));
      }
      setTimeout(() => {
        if (!Number.isInteger(+value)) {
          callback(new Error("手机号必须为数字"));
        } else {
          if (phoneReg.test(value)) {
            callback();
          } else {
            callback(new Error("手机号码格式不正确"));
          }
        }
      }, 100);
    };
    return {
      btntxt:"获取验证码",
      isDisabled:false,
      time:0,
      formLabelWidth: '130px',
      registerForm: {
        studentCode: "",
        userName: "",
        phoneNumber: "",
        smsCode: "",
        file: "",
        binaryFile: null, 
        password: ""
      },
      rules: {
        studentCode: [
          { required: true, validator: checkStudentCode, trigger: "blur" }
        ],
        userName: [
          { required: true, message: "请输入用户名", trigger: "blur" }
        ],
        phoneNumber: [
          { required: true,  validator: checkPhoneNumber,trigger: "blur" }
        ],
        smsCode: [
          { required: true, message: "请输入短信验证码", trigger: "blur" },
          // { min: 3, max: 5, message: "长度在 3 到 5 个字符", trigger: "blur" }
        ],
        file: [{ required: true, message: "请选择头像" }],
        password: [
          { required: true, message: "请输入用户密码", trigger: "blur" },
          { min: 6, max: 20, message: "密码长度在 6 到 20 个字符", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    fileRequest(item) {
        this.registerForm.binaryFile = item.file;
        // this.registerForm.file = '21312312';
        // console.log('this.registerForm.file', this.sellForm.file)
        // console.log('typeof this.registerForm.binaryFile', this.registerForm.binaryFile);
      },
    //验证手机号码部分
    sendCode(){
      // console.log('ok')
      this.time=60;
      this.disabled=true;
      this.timer();
      this.getSmsCode();
    },
    timer(){
        if (this.time > 0) {
            this.time--;
            this.btntxt=this.time+"s后重新获取";
            setTimeout(this.timer, 1000);
        } else{
            this.time=0;
            this.btntxt="获取验证码";
            this.isDisabled=false;
        }
    },
    // 获取验证码
    getSmsCode(){
        this.$http({
            url: "/api/smsCode",
            method: "post",
            crossdomain: true,
            headers: {
                'Content-Type': 'application/json',
            },
            data: {
              phoneNumber: this.registerForm.phoneNumber,
            }
            }).then(res => {
              console.log(res)
            console.log('验证码成功获取');
          }).catch(res=>{
            console.log(res)
            console.log('error')
          });
    },
    // 对图片的处理
    handleAvatarSuccess(filed) {
        this.registerForm.file = URL.createObjectURL(filed.raw);
    },
    beforeAvatarUpload(filed) {
        const isJPG = filed.type === 'image/jpeg';
        const isLt2M = filed.size / 1024 / 1024 < 2;
        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isJPG && isLt2M;
    },
    // 下一步
    submitForm(formName) {
      this.$refs.registerForm.validate((valid) => {
        if (valid) {
          this.$router.push({path: '/register2',
          query: {
              studentCode:this.registerForm.studentCode,
              userName:this.registerForm.userName,
              phoneNumber:this.registerForm.phoneNumber,
              smsCode:this.registerForm.smsCode,
              file:this.registerForm.file,
              password:this.registerForm.password
            }}) 
        } else {
          this.$message.error('Error !!');
          return false;
        }
      });
    },
  }
}
</script>
