<template>
  <div class="home">
    <div>
      <!-- 顶部导航栏区域 -->
      <div class="nav">
        <p class="welcome">您好！欢迎来到西邮二手书交易网！</p>
        <div class="navigat">
          <ul>
           <li><router-link to="/personal">我的主页</router-link></li> 
            <li><router-link to="/shopcar">购物车</router-link></li>    
            <li ><router-link :to="token?'/kefu': '/login'">{{token}}</router-link></li>   
            <li><div @click="logout">退出</div></li> 
          </ul>
        </div>
      </div>
      <!-- 中间区域 -->
      <div class="oldtitle">
        <div class="h1">
          <h1>我的购物车</h1>
        </div>
        <div class="navtitle">
          <ul>
            <router-link to="/home">首页</router-link>
            <router-link to="/sell">出售</router-link>
            <router-link to="/orderList">我的订单</router-link>
            <router-link to="/fabued">我的发布</router-link>
          </ul>
        </div>
      </div>
      <!-- 主页中心 -->
      <div class="shopmain">
        <div class="mainone">
          <div class="maintwo">
            <div class="myShopCar">
              <div>
                <h1>我的下单</h1>
              </div>
              <div class="input">
                    <el-select v-model="queryInfo.message" clearable  @change="getOrderBook" placeholder="查询我的订单">
                      <el-option
                        v-for="item in options"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                      </el-option>
                  </el-select>
              </div>
            </div>
            <div class="myShops">
              <el-table :data="orderList" stripe style="width: 100%">
                <el-table-column type="index" label="序号" width="60" align="center"></el-table-column>
                <el-table-column prop="book.bookName" label="书名" width="130" align="center"></el-table-column>
                <el-table-column prop="book.userName" label="卖家" width="120" align="center"></el-table-column>
                <el-table-column prop="book.price" label="单价/(元)" width="100" align="center"></el-table-column>
                <el-table-column prop="buyDate" label="下单时间" width="140" align="center">
                  <template slot-scope="scope">{{scope.row.buyDate|dataFormat}}</template>
                </el-table-column>
                <el-table-column label="类型"  width="140" align="center">
                  <template slot-scope="scope">{{scope.row.book.bookStyle.join('、')}}</template>
                </el-table-column>
                <el-table-column prop="orderStatus.status" label="订单状态" width="120" align="center">
                  <template slot-scope="scope">
                  <el-tag
                    v-if="scope.row.orderStatus.status===1"
                  >等待支付</el-tag>
                  <el-tag
                    v-if="scope.row.orderStatus.status===2" 
                    type="success"
                  >支付成功</el-tag>
                  <el-tag
                    v-if="scope.row.orderStatus.status===3"
                  type="warning"
                  >已取消</el-tag>
                  <el-tag
                    v-if="scope.row.orderStatus.status===4"
                    type="danger"
                  >已过期</el-tag>
                  <el-tag
                    v-if="scope.row.orderStatus.status===5"
                  type="success"
                  >已完成</el-tag>
                  </template>
                </el-table-column>
                <el-table-column label="操作" width="230" align="center">
                  <template slot slot-scope="scope">
                    <!-- <el-button
                      type="danger"
                      size="mini"
                      @click="discre(scope.row.bookId)"
                    >查看详情</el-button> -->
                    <el-button
                      type="primary"
                      size="mini"
                      @click="comfirm(scope.row.orderId)"
                      :disabled="scope.row.orderStatus.status!=2"
                    >确认收货</el-button>
                  </template>
                  <!-- v-if="scope.row.orderStatus.status===2?disabled=true:disabled=false"  -->
                </el-table-column>
              </el-table>
              <div class="pagination">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="queryInfo.page"
                  :page-sizes="[8,15,25,40]"
                  :page-size="queryInfo.size"
                  layout="total, sizes, prev, pager, next, jumper"
                  :total="total"
                ></el-pagination>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
<!-- <footer-box></footer-box> -->
</template>
<script>
import footer from '../components/footer.vue'
export default {
  data() {
    return {
      orderList: [],
      checked: true,
      num: 1,
      total: 0,
      token:'客服',
      // disabled:true,
      message: "", //serch查询的数据
      queryInfo: {
        message:'',
        page: 1, //页数
        size: 15, //当前页数的数据个数
      },
      status:0,
      options: [{
              value: '1',
              label: '等待支付的订单'
            }, {
              value: '2',
              label: '成功支付的订单'
            }, {
              value: '3',
              label: '已取消的订单'
            }, {
              value: '4',
              label: '已过期的订单'
            }, {
              value: '5',
              label: '已完成的订单'
            }],
    };
  },
  // inject: ['reload'],
  components:{
        "footer-box":footer
    },
  created() {
    this.getAllorders();
    
  },
  methods: {
    // 确认收货
    async comfirm(orderId){
      const confirm = await this.$confirm(
        "该操作将确认此商品已收货, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }
      ).catch(err => err);
      if (confirm !== "confirm") {
        return this.$message.info("已取消收货");
      }
      this.$http({
            url: `http://49.234.70.67:8081/u/ok/order/${orderId}`,
            method: "post",
            crossdomain: true,
            headers: {
                'Content-Type': 'application/json',
                'token':window.sessionStorage.getItem('token')
            },
            }).then(res => {
              console.log(res)
              if(!res.config.headers.token){
                this.token = '登陆/注册'
              } 
              this.getAllorders()
              // 点击确认收货后  标签值变成完成  刷新列表   
              if(res.data.status === -1) {
                  this.$message.error(res.data.message);
                  this.$router.push('/login')
              }
              if(res.data.status === 201) {
                  this.$router.push('/login')
              }
            }).catch(res=>{  
              console.log('error')
            })
    },
    // 查询所有订单
    getAllorders() {
      this.$http({
        url: "http://49.234.70.67:8081/u/search/order",
        method: "post",
        crossdomain: true,
        headers: {
          "Content-Type": "application/json",
          token: window.sessionStorage.getItem("token"),
        },
        data: this.queryInfo,
        })
        .then((res) => {
          console.log(res);
          this.orderList = res.data.data[0];
          this.total = res.data.data[1].total
          if (res.data.status === -1) {
            this.$message.error(res.data.message);
            this.$router.push("/login");
          }
        })
        .catch((res) => {
          console.log("未获取到订单");
        });
    },
    // 查询订单 ---  状态
    getOrderBook() {
      this.$http({
        url: "http://49.234.70.67:8081/u/search/order/style",
        method: "post",
        crossdomain: true,
        headers: {
          "Content-Type": "application/json",
          token: window.sessionStorage.getItem("token"),
        },
        data:this.queryInfo
      })
        .then((res) => {
          console.log(res);
          this.orderList = res.data.data[0]
          if (res.data.status === -1) {
            this.$message.error(res.data.message);
            this.$router.push("/login");
          }
        })
        .catch((res) => {
          console.log("未获取到订单");
        });
    },
    // 点击查看详情
    // discre(bookId) {
    //   this.$http({
    //     url: `http://49.234.70.67:8081/u/search/book/${bookId}`,
    //     method: "get",
    //     crossdomain: true,
    //     headers: {
    //       "Content-Type": "application/json",
    //       token: window.sessionStorage.getItem("token"),
    //     },
    //   })
    //     .then((res) => {
    //       console.log(res);
    //       // this.$router.push('/buy')
          
    //       if (res.data.status === -1) {
    //         this.$message.error(res.data.message);
    //         this.$router.push("/login");
    //       }
    //     })
    //     .catch((res) => {
    //       console.log("未获取到订单");
    //     });
    // },
      // 分页
    handleSizeChange(newsize) {
        this.queryInfo.size = newsize;
        this.getAllorders();
    },
    handleCurrentChange(newnum) {
        this.queryInfo.page = newnum;
        this.getAllorders();
    },
    handleChange() {},
    // 退出
    logout() {
      this.$http({
        url: "http://49.234.70.67:8081/u/logout",
        method: "get",
        crossdomain: true,
        headers: {
          token: window.sessionStorage.getItem("token"),
        },
      })
        .then((res) => {
          console.log(res);
          window.sessionStorage.clear();
          this.$router.push("/login");
          if (res.data.status === -1) {
            this.$message.error(res.data.message);
            this.$router.push("/login");
          }
        })
        .catch((res) => {
          console.log("未获取到");
        });
    },
  },
};
</script>
<style  scoped>
.oldtitle {
  width: 100%;
  height: 80px;
  background-color: #fff;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.oldtitle .h1 {
  color: rgb(1, 200, 181);
}
.navtitle ul a {
  text-decoration: none;
  color: rgb(100, 100, 100);
  font-size: 18px;
  font-weight: bold;
  margin-right: 70px;
}
.navtitle ul a:hover {
  color: rgb(1, 200, 181);
}
/* 中间区域 */
.shopmain .mainone {
  background-color: rgb(245, 245, 245);
  width: 100%;
  /* border:1px red solid; */
  /* height: 500px; */
  padding: 10px;
}
.shopmain .maintwo {
  margin: 0 auto;
  width: 1000px;
  height: 40px;
  background-color: #fff;
  padding: 15px;
}
.myShopCar {
  display: flex;
  justify-content: space-between;
}
.myShopCar h1 {
  color: rgb(119, 119, 119);
}
.input {
  width: 400px;
}
.maintwo .myShops {
  width: 1035px;
  margin: 20px auto;
  margin-left: -17px;
  background-color: #fff;
}
.qujiesuan {
  display: flex;
  width: inherit;
  border: 1px #fff solid;
  height: 17px;
  justify-content: space-between;
  align-items: center;
  margin-top: -21px;
  box-sizing: content-box;
  padding: 18px;
  margin-left: -18px;
  color: rgb(144, 147, 153);
}
.qujiesuan b {
  color: red;
}
.populate {
  width: 300px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.populate .qusuan {
  background-color: rgb(1, 200, 181);
  height: 25px;
  color: #fff;
  padding: 5px;
  border-radius: 3px;
  font-weight: bold;
}
