                </el-table>
                <div class="pagination">
                  <el-pagination
                    @size-change="handleSizeChange1"
                    @current-change="handleCurrentChange1"
                    :current-page="queryInfo1.page"
                    :page-sizes="[3,5,10,15]"
                    :page-size="queryInfo1.size"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total1">
                  </el-pagination>
              </div>
              </el-tab-pane>
              <el-tab-pane label="已卖出">
                <el-table :data="haveSeltList" stripe style="width: 100%">
                  <el-table-column prop="bookName" label="书名" width="100" align="center"></el-table-column>
                  <el-table-column prop="bookSrc" label="图片" width="100" align="center">
                      <template slot-scope="scope">
                        <el-image
                        style="width: 100px; height: 100px"
                        :src="'http://49.234.70.67:8081/'+scope.row.bookSrc"></el-image>
                      </template>
                  </el-table-column>
                  <el-table-column prop="buyerName" width="100" label="买家昵称" align="center"></el-table-column>
                  <el-table-column prop="buyerStudentCode" width="100" label="买家学号" align="center"></el-table-column>
                  <el-table-column prop="buyDate" label="时间" align="center">
                    <template slot-scope="scope">{{scope.row.bookDate|dataFormat}}</template>
                  </el-table-column>
