</template>
<script>
import footer from '../components/footer.vue'
export default {
  data() {
    return {
      imageUrl: '',
      tableData:'',
      tabPosition: "left",
      UserInfos:{},
      total1:0,
      total2:0,
      queryInfo1: {
        page: 1, //页数
        size:5  //当前页数的数据个数
      },
      queryInfo2: {
        page: 1, //页数
        size:5  //当前页数的数据个数
      },
      willFahuoList:[],//待发货
      haveSeltList:[],//已卖出
      token:'客服'
    };
  },
  components:{
        "footer-box":footer
    },
  created(){
    // 获取用户信息
    this.getUserInfo();
    // 待发货
    this.willFahuo()
    // 已卖出
    this.haveSelt()
  },
  methods:{
    // 获取我的主页信息
    getUserInfo(){
      this.$http({
            url: "http://49.234.70.67:8081/u/home",
            method: "get",
            crossdomain: true,
            headers: {
                'token':window.sessionStorage.getItem('token')
            },
            }).then(res => {
              console.log(res)
              if(!res.config.headers.token){
                this.token = '登陆/注册'
              } 
              if (res.data.status === -1) {
                  return this.$message.error(res.data.message);
                  this.$router.push('/login')
              }
              this.UserInfos = res.data.data[0]
              console.log('我的主页数据获取成功')
              console.log(this.UserInfos)
            }).catch(res=>{
              console.log('未获取到')
            })
    },
    // 待发货
    willFahuo(){
      this.$http({
            url: "http://49.234.70.67:8081/u/search/message",
            method: "post",
            crossdomain: true,
            headers: {
                'Content-Type': 'application/json',
                'token':window.sessionStorage.getItem('token')
            },
            data:this.queryInfo1
            }).then(res => {
              console.log('待发货',res)
              if (res.data.status === 200) {
                  this.willFahuoList=res.data.data[0]
                  this.total1 = this.willFahuoList.length
              }
              if (res.data.status === -1) {
                  this.$message.error(res.data.message);
                  this.$router.push('/login')
              }
            }).catch(res=>{
              console.log('未获取到')
            })
    },
    // 已卖出
    haveSelt(){
       this.$http({
            url: "http://49.234.70.67:8081/u/search/sell",
            method: "post",
            crossdomain: true,
            headers: {
                'Content-Type': 'application/json',
                'token':window.sessionStorage.getItem('token')
            },
            data:this.queryInfo2
            }).then(res => {
              console.log('已卖出',res)
              if (res.data.status === 200) {
                  this.haveSeltList=res.data.data[0]
                  this.total2 = this.haveSeltList.length
              }
              if (res.data.status === -1) {
                  this.$message.error(res.data.message);
                  this.$router.push('/login')
              }
            }).catch(res=>{
              console.log('未获取到')
            })
    },
    // 分页
    handleSizeChange1(newsize) {
        this.queryInfo1.page = newsize;
        this.willFahuo();
    },
    handleCurrentChange1(newnum) {
        this.queryInfo1.page = newnum;
        this.willFahuo();
    },
    handleSizeChange2(newsize) {
        this.queryInfo2.page = newsize;
        this.haveSelt();
    },
    handleCurrentChange2(newnum) {
        this.queryInfo2.page = newnum;
        this.haveSelt();
    },
      // 退出
      logout(){
        this.$http({
            url: "http://49.234.70.67:8081/u/logout",
            method: "get",
            crossdomain: true,
            headers: {
                'token':window.sessionStorage.getItem('token')
            }
            }).then(res => {
              console.log(res)
              window.sessionStorage.clear();
              this.$router.push("/login");
              if (res.data.status === -1) {
                  this.$message.error(res.data.message);
                  this.$router.push('/login')
              }
            }).catch(res=>{
              console.log('未获取到')
            })
      },
    }
};
</script>
