<template>
  <div class="home">
    <div>
      <!-- 顶部导航栏区域 -->
      <div class="nav">
        <p class="welcome">您好！欢迎来到西邮二手书交易网！</p>
        <div class="navigat">
          <ul>
            <li><router-link to="/personal">我的主页</router-link></li> 
            <li><router-link to="/shopcar">购物车</router-link></li>    
            <li ><router-link :to="token?'/kefu': '/login'">{{token}}</router-link></li>   
            <li><div @click="logout">退出</div></li> 
          </ul>
        </div>
      </div>
      <!-- 中间区域 -->
      <div class="oldtitle">
        <div class="h1">
          <h1>我的购物车</h1>
        </div>
        <div class="navtitle">
          <ul>
          <router-link to="/home">首页</router-link>
            <router-link to="/sell">出售</router-link>
            <router-link to="/orderList">我的订单</router-link>
            <router-link to="/fabued">我的发布</router-link>
          </ul>
        </div>
      </div>
      <!-- 主页中心 -->
      <div class="shopmain">
        <div class="mainone">
          <div class="maintwo">
            <div class="myShopCar">
              <h1>填写订单</h1>
            </div>
            <div class="myShops">
                <div>
                    <h2 style="margin-bottom:10px">收货人信息</h2>
                    <div>
                        <span style="color:rgb(100,100,100)">陕西西安</span>&nbsp;&nbsp;&nbsp;
                        <span style="color:rgb(100,100,100)">骆嘉敏 收</span>&nbsp;&nbsp;&nbsp;
                        <span style="color:rgb(100,100,100)">15769187423</span>&nbsp;&nbsp;
                        <!-- <hr style="margin-top:10px"> -->
                    </div>
                </div>
                <div class="toSet" @click="dialogVisible = true">立即设置</div>
                <el-dialog
                  title="设置收货地址"
                  :visible.sync="dialogVisible"
                  width="50%"
                  :before-close="handleClose">
                  <span>这是一段信息</span>
                  <span slot="footer" class="dialog-footer">
                    <el-button @click="dialogVisible = false">取 消</el-button>
                    <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
                  </span>
                </el-dialog>
            </div>
            <div class="content">
              <el-table :data="orderCarList" stripe style="width: 100%;margin-top:20px" @selection-change="handleSelectionChange">
                <el-table-column type="selection" width="60" align="center"></el-table-column>
                  <el-table-column type="index" label="序号" width="65" align="center"></el-table-column>
                  <el-table-column prop="bookName" label="书名" width="150" align="center"></el-table-column>
                  <el-table-column prop="bookSrc" label="图片" width="150" align="center">
                      <template slot-scope="scope">
                          <el-image
                          style="width: 70px; height: 80px"
                          :src="'http://49.234.70.67:8081/'+scope.row.bookSrc"></el-image>
                      </template>
                  </el-table-column>
                  <el-table-column prop="price" label="价格" width="120" align="center"></el-table-column>
                  <el-table-column prop="number" label="购买数量" width="110" align="center"></el-table-column>
                  <el-table-column label="小计/(元)" width="120" align="center">
                    <template slot slot-scope="scope">
                        {{scope.row.price*scope.row.number}} 
                      </template>
                  </el-table-column>
                  <el-table-column label="操作" width="260" align="center">
                    <template slot slot-scope="scope">
                      <el-button
                      type="primary"
                      icon="el-icon-delete"
                      size="mini"
                      @click="deletePay(scope.row.orderId)"
                    >取消</el-button>
                  <el-button
                      type="danger"
                      size="mini"
                      icon="el-icon-circle-check"
                      @click="toPay(scope.row.orderId)"
                    >结算 </el-button>
                    <!-- <el-button
                      type="warning"
                      size="mini"
                      style="margin-top:5px"
                      icon="el-icon-circle-close"
                      plain
                      v-if="flag == 1?deletePays(scope.row.orderId):''"
                      
                    >{{minute}}:{{second}}后订单自动取消</el-button> -->
                    </template>
                  </el-table-column>
              </el-table>
            </div>
            <div class="qujiesuan">
                  <div></div>
                  <div class="populate">
                    <span class="go" @click="toShopCar"> 返回购物车 </span>
                    <span>实付款: <b>￥{{payMoney}}</b></span>
                    <div class="qusuan" @click="next"> 支付</div>
                  </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- <footer-box></footer-box> -->
  </div>
</template>
<script>
// import footer from '../components/footer.vue'
export default {
  data() {
    return {
      orderCarList:[],
      checked:true,
      // num:1,
      dialogVisible: false,
      bookIds:this.$route.query.bookIds,
      number:this.$route.query.number,
      multipleSelection: [], //存放已选择的数据
      orderIds:[],
      token:'客服',
      // minutes:1,
      // seconds:0,
      // // displayTime:true,
      // flag:0
    };
  },
  // components:{
  //       "footer-box":footer
  //   },
  created(){
    // 生成订单  发送一个数组对象
    this.getAllmyOrders()
  },
  // mounted(){
  //     this.timer()
  // },
  computed:{
    payMoney:function(index){
        var price_total = 0;
        for (var i = 0; i < this.multipleSelection.length; i++) {
          price_total +=
           this.multipleSelection[i].price * this.multipleSelection[i].number;
          }
        return price_total;
    },
    // second:function () {
    //       return this.num(this.seconds)
    // },
    // minute:function () {
    //       return this.num(this.minutes)
    // },
  },
  // watch: {
  //     second: {
  //       handler(newVal) {
  //         this.num(newVal)
  //       }
  //     },
  //     minute: {
  //       handler(newVal) {
  //         this.num(newVal)
  //       }
  //     }
  // },
  methods: {
    //     num(n) {
    //         return n < 10 ? '0' + n : '' + n
    //     },
    //     timer () {
    //       var _this = this
    //       var time = window.setInterval(function () {
    //           if (_this.seconds === 0 && _this.minutes !== 0) {
    //             _this.seconds = 59
    //             _this.minutes -= 1
    //           } else if (_this.minutes === 0 && _this.seconds === 0) {
    //             // _this.seconds = 0
    //             window.clearInterval(time)
    //              // 隐藏掉 有点不起作用
    //             this.displayTime = false
    //             // 此时我该传什么参数
    //             // this.deletePays()
    //             this.flag == 1
    //           } else {
    //             _this.seconds -= 1
    //           }
    //       }, 1000)
    // },
    // 返回购物
    toShopCar(){
      this.$router.push('/shopcar')
    },
    // 获取我的订单
    getAllmyOrders(){
          const arr={
            bookIds:this.bookIds,
            number:this.number
          }
          this.$http({
            url: "http://49.234.70.67:8081/u/make/order",
            method: "post",
            crossdomain: true,
            headers: {
                'Content-Type': 'application/json',
                'token':window.sessionStorage.getItem('token')
            },
            data:arr
            }).then(res => {
              console.log(res)
              if(!res.config.headers.token){
                this.token = '登陆/注册'
              } 
              this.orderCarList = res.data.data[0]
              if (res.data.status === -1) {
                  this.$message.error(res.data.message);
                  this.$router.push('/login')
              }
              if (res.data.status === -7) {
                  this.$message.error(res.data.message);
                  this.$router.push('/shopcar')
              }
            }).catch(res=>{
              console.log('未获取到')
            })
    },
    handleSelectionChange(val){
      this.multipleSelection = val;
      for(let i=0;i<this.multipleSelection.length;i++){
        if(this.orderIds.indexOf(this.multipleSelection[i].orderId)==-1){
          this.orderIds.push(this.multipleSelection[i].orderId)
        }
      }
    },
    // 付款一群
    next(){
        // toPay()
        // console.log(this.orderIds)
        if(this.multipleSelection.length==0){
            // console.log('无要购买的书籍')
            this.$message.error('请先选择要购买的书籍');
            return;
        }
        this.$http({
            url: 'http://49.234.70.67:8081/u/pay/now',
            method: "post",
            crossdomain: true,
            headers: {
                'Content-Type': 'application/json',
                'token':window.sessionStorage.getItem('token')
            },
            data:this.orderIds
            }).then(res => {
              console.log(res)
            
              if (res.data.status === -1) {
                  this.$message.error(res.data.message);
                  this.$router.push('/login')
              }else if(res.data.status === -8){
                  this.$message.error('订单已过期');
              }else if(res.data.status === 200){
                  this.$message.success('支付成功');
                  this.$router.push('/orderList')
              }
            }).catch(res=>{
              console.log('未获取到')
            })
    },
    // 支付一个
    toPay(orderId){
          console.log(orderId)
          this.$http({
            url: `http://49.234.70.67:8081/u/pay/order/${orderId}`,
            method: "post",
            crossdomain: true,
            headers: {
                'Content-Type': 'application/json',
                'token':window.sessionStorage.getItem('token')
            },
            }).then(res => {
              console.log(res)
              // this.$router.push('/orderList')
              if (res.data.status === -1) {
                  this.$message.error(res.data.message);
                  this.$router.push('/login')
              }else if(res.data.status === -8){
                  this.$message.error('订单已过期');
              }else if(res.data.status === 200){
                  this.$message.success('支付成功');
                  this.$router.push('/orderList')
              }
            }).catch(res=>{
              console.log('未获取到')
            })
    },
    // 立即设置
    handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => {});
    },
    // 长时间未操作  订单取消
    async deletePays(orderId){
        const confirm = await this.$confirm(
        "15分钟未付款订单即将取消, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }
      ).catch(err => err);
      if (confirm !== "confirm") {
        return this.$message.info("已取消删除");
      }
      this.$http({
            url: `http://49.234.70.67:8081/u/cancel/${orderId}`,
            method: "post",
            crossdomain: true,
            headers: {
                'Content-Type': 'application/json',
                'token':window.sessionStorage.getItem('token')
            },
            }).then(res => {
              console.log(res)
              if (res.data.status === 200) {
                  this.$message.success('取消成功！');
                  this.getAllmyOrders()
              }
              if (res.data.status === -1) {
                  this.$message.error(res.data.message);
                  this.$router.push('/login')
              }
            }).catch(res=>{
              console.log('error')
            })
    },
    // 取消一个待支付的订单
    async deletePay(orderId){
        const confirm = await this.$confirm(
        "此操作将永久取消该商品, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }
      ).catch(err => err);
      if (confirm !== "confirm") {
        return this.$message.info("已取消删除");
      }
      this.$http({
            url: `http://49.234.70.67:8081/u/cancel/${orderId}`,
            method: "post",
            crossdomain: true,
            headers: {
                'Content-Type': 'application/json',
                'token':window.sessionStorage.getItem('token')
            },
            }).then(res => {
              console.log(res)
              if (res.data.status === 200) {
                  this.$message.success('取消成功！');
                  this.getAllmyOrders()
              }
              if (res.data.status === -1) {
                  this.$message.error(res.data.message);
                  this.$router.push('/login')
              }
            }).catch(res=>{
              console.log('error')
            })
    },
    // 退出
    logout(){
        this.$http({
            url: "http://49.234.70.67:8081/u/logout",
            method: "get",
            crossdomain: true,
            headers: {
                'token':window.sessionStorage.getItem('token')
            }
            }).then(res => {
              console.log(res)
              window.sessionStorage.clear();
              this.$router.push("/login");
              if (res.data.status === -1) {
                  this.$message.error(res.data.message);
                  this.$router.push('/login')
              }
            }).catch(res=>{
              console.log('未获取到')
            })
      },
  }
};
</script>
<style  scoped>
.oldtitle {
  width: 100%;
  height: 80px;
  background-color: #fff;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.oldtitle .h1 {
  color: rgb(1, 200, 181);
}
.navtitle ul a {
  text-decoration: none;
  color: rgb(100, 100, 100);
  font-size: 18px;
  font-weight: bold;
  margin-right: 70px;
}
.navtitle ul a:hover {
  color: rgb(1, 200, 181);
}
/* 中间区域 */
.shopmain .mainone {
  background-color: rgb(245, 245, 245);
  width: 100%;
  /* border:1px red solid; */
  /* height: 500px; */
  padding: 10px;
}
.shopmain .maintwo {
  margin: 0 auto;
  width: 1000px;
  height: 40px;
  background-color: #fff;
  padding: 15px;
}
.myShopCar h1 {
  color: rgb(119, 119, 119);
}
.maintwo .myShops {
  width: 1035px;
  margin: 20px auto;
  margin-left: -17px;
  background-color: #fff;
  display: flex;
  align-items: center;
  padding:10px;
  justify-content: space-between;
  box-sizing: border-box;
}

.qujiesuan{
  display: flex;
  width:inherit;
  border:1px #fff solid;
  height: 17px;
  justify-content:space-between;
  align-items: center;
  margin-top:-1px;
  box-sizing: content-box;
  padding:18px;
  margin-left: -18px;
  color:rgb(144,147,153)
}
.qujiesuan b{
  color:red
}
.populate{
  width:300px;
  display: flex;
   align-items: center;
  justify-content:space-between;
}
.populate .qusuan{
  background-color: rgb(1, 200, 181);
  height: 25px;
  color:#fff;
  padding:5px;
  border-radius: 3px;
  font-weight: bold;
}
.populate .go{
    color:rgb(1, 200, 181);
    font-size: 12px;
}
.toSet{
    color:rgb(1, 200, 181);
}
.content{
  width:1040px;
  margin-top: -10px;
  margin-left: -20px;
}
</style>