
<template>
<!-- 发布书籍 -->
    <div class="home">
    <div>
    <!-- 顶部导航栏区域 -->
        <div class="nav">
        <p class="welcome">您好！欢迎来到西邮二手书交易网！</p>
        <div class="navigat">
          <ul>
            <li><router-link to="/personal">我的主页</router-link></li> 
            <li><router-link to="/shopcar">购物车</router-link></li>    
            <li ><router-link :to="token?'/kefu': '/login'">{{token}}</router-link></li>   
            <li><div @click="logout">退出</div></li>     
          </ul>
        </div>
      </div>
      <!-- 中间区域 -->
        <div class="oldtitle">
        <div class="h1">
          <h1>二手书交易网</h1>
        </div>
        <div class="navtitle">
        <ul>
            <router-link to="/home">首页</router-link>
            <router-link to="/sell">出售</router-link>
            <router-link to="/orderList">我的订单</router-link>
            <router-link to="/fabued">我的发布</router-link>
          </ul>
        </div>
      </div>
      <!-- 主页中心 -->
      <div class="page">
        <div class="pageleft">
        </div>
        <div class="pageright">
          <div class="recommend">
              <h2>我的出售</h2>
          </div>
        <div class="mySell">
            <div class="mySellItem">
              <el-form :model="sellForm" :rules="sellRules" ref="sellForms" label-width="80px" class="demo-ruleForm">
                <el-form-item label="书名" prop="bookName">
                  <el-input v-model="sellForm.bookName"></el-input>
                </el-form-item>
                <el-form-item label="类型" prop="bookStyle">
                    <el-select v-model="sellForm.bookStyle" multiple placeholder="请选择类型">
                      <el-option
                        v-for="item in options"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                      </el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="封面图片" prop="file">
                  <el-upload
                    class="avatar-uploader"
                    :action= actionUp
                    :show-file-list="false"
                    :on-change="handleAvatarSuccess"
                    :before-upload="beforeAvatarUpload"
                    :http-request="fileRequest"
                  >
                  <img v-if="sellForm.file" :src="sellForm.file" class="avatar">
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
                </el-form-item>
                <el-form-item label="价格" prop="price">
                  <el-input-number v-model="sellForm.price"  :precision="2" :step="0.1" :max="100000"></el-input-number>
                </el-form-item>
                <el-form-item label="简介" prop="bookIntroduction">
                  <textarea class="textarea"  v-model="sellForm.bookIntroduction"  name id cols="51" rows="10" placeholder="(请填写书籍简介,不超过200字)"></textarea>
                </el-form-item>
                <el-form-item label="库存" prop="stock">
                  <el-input-number v-model="sellForm.stock" controls-position="right" @change="handleChange" :min="1" :max="10000"></el-input-number>
                </el-form-item>
            </el-form>
            </div>
            <div class="sellbooks">  
                  <el-button type="primary" plain @click="onFaBu">发布</el-button>
              </div>
          </div>
        </div>
      </div>
    </div>
    <footer-box></footer-box>
</div>
</template>
<script>
import footer from '../components/footer.vue'
export default {
    data(){
      return {
        num: 1,
        num1:1,
        options: [{
              value: '科幻',
              label: '科幻'
            }, {
              value: '悬疑',
              label: '悬疑'
            }, {
              value: '灾难',
              label: '灾难'
            }, {
              value: '爱情',
              label: '爱情'
            }, {
              value: '计算机',
              label: '计算机'
            }, {
              value: '数学',
              label: '数学'
            }, {
              value: '电子',
              label: '电子'
            }, {
              value: '历史',
              label: '历史'
            }, {
              value: '文艺',
              label: '文艺'
            }, {
              value: '网络',
              label: '网络'
            }],
        value1: [],
        token:'客服',
        actionUp:'https://www.mocky.io/v2/5185415ba171ea3a00704eed/posts/',
        sellForm: {
          bookName: '',
          bookStyle	: '',
          bookIntroduction: '',
          price: '',
          file: '',
          binaryFile: null,
          stock: ''
        },
        sellRules: {
          bookName: [
            { required: true, message: '请输入书籍名称', trigger: 'blur' },
            { min: 1, max: 20, message: '长度在 1 到 20 个字符', trigger: 'blur' }
          ],
          bookStyle: [
            { required: true, message: '请选择书籍类型', trigger: 'blur' }
          ],
          bookIntroduction: [
            { required: true, message: '请输入书籍简介', trigger: 'blur' },
            { min: 3, max: 200, message: '长度在 3 到 200 个字符', trigger: 'blur' }
          ],
          price: [
            { required: true, message: '请输入书籍价格', trigger: 'blur' },
          ],
          file: [
            { required: true, message: '请选择封面图片', trigger: 'blur' },
          ],
          stock: [
            { required: true, message: '请输入库存', trigger: 'blur' }
          ],
        }
      }
    },
    components:{
        "footer-box":footer
    },
    methods: {
      // 书籍封面图片存储
      fileRequest(item) {
        this.sellForm.binaryFile = item.file;
        // this.sellForm.file = '12321321'
        // console.log('this.sellForm.file', this.sellForm.file)
        // console.log('typeof this.sellForm.binaryFile', this.sellForm.binaryFile);
      },
      // 发布书籍
      onFaBu(){
            const bookName = this.sellForm.bookName
            const bookStyle = this.sellForm.bookStyle
            const bookIntroduction = this.sellForm.bookIntroduction
            const price = this.sellForm.price.toFixed(2)
            const stock = this.sellForm.stock
            const binaryFile = this.sellForm.binaryFile
            console.log('binaryFile', binaryFile);
            console.log('price',price)
            let param = new FormData()  // 创建form对象
            param.append('file', binaryFile)  // 通过append向form对象添加数据
            param.append('bookName',bookName) // 添加form表单中其他数据
            param.append('bookStyle',bookStyle)
            param.append('bookIntroduction',bookIntroduction)
            param.append('price',price)
            param.append('stock',stock)
            console.log(param.get('file'))
        this.$refs.sellForms.validate(async valid=>{
        if(!valid) return;  
          this.$http({
            url: "http://49.234.70.67:8081/u/release",
            method: "post",
            crossdomain: true,
            headers: {
                  'Content-Type': 'multipart/form-data' ,
                  transformRequest: [data => data],
                  'token':window.sessionStorage.getItem('token')
            },
            data: param
            }).then(res => {
              console.log(res)
              this.sellForm.bookName = ''
              this.sellForm.bookStyle = ''
              this.sellForm.bookIntroduction = ''
              this.sellForm.price = ''
              this.sellForm.stock = ''
              this.sellForm.file = null;
                if (res.data.status === -1) {
                      this.$message.error(res.data.message);
                      this.$router.push('/login')
                  }else{
                        this.$message.success('发布成功！')
                        this.$router.push('/fabued')
                  }
          }).catch(res => {
                  console.log('error')
          });
        })
      },
      //  选择类型
      handleCommand(command) {
        this.$message('click on item ' + command);
      },
      // 上传图片
      handleAvatarSuccess(filed) {
        this.sellForm.file = URL.createObjectURL(filed.raw);
        // console.log('url', this.sellForm.file)
      },
      beforeAvatarUpload(filed) {
        const isJPG = filed.type === 'image/jpeg';
        const isLt2M = filed.size / 1024 / 1024 < 2;
        if (!isJPG) {
          this.$message.error('上传图片只能是 JPG 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传图片大小不能超过 2MB!');
        }
        return isJPG && isLt2M;
      },
      handleChange(value) {
        console.log(value);
      },
        // 退出
      logout(){
        this.$http({
            url: "http://49.234.70.67:8081/u/logout",
            method: "get",
            crossdomain: true,
            headers: {
                'token':window.sessionStorage.getItem('token')
            }
            }).then(res => {
              console.log(res)
              window.sessionStorage.clear();
              this.$router.push("/login");
              if (res.data.status === -1) {
                  this.$message.error(res.data.message);
                  this.$router.push('/login')
              }
            }).catch(res=>{
              console.log('未获取到')
            })
      },
    }
}
</script>
<style scoped>
.oldtitle {
  width: 100%;
  height: 80px;
  background-color: #fff;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.oldtitle .h1 {
  color: rgb(1, 200, 181);
}
.navtitle ul a {
  text-decoration: none;
  color: rgb(100, 100, 100);
  font-size: 18px;
  font-weight: bold;
  margin-right: 70px;
}
.navtitle ul a:hover {
  color: rgb(1, 200, 181);
}
.page{
  width:100%;
  display: flex;
  padding: 10px 100px;
  margin-top: 10px;
  background-color: rgb(247,247,246);
}
.page .pageleft{
  width:25%;
  height:400px;
  margin-right:15px;
  background-color:#fff;
}
.page .pageright{
  width:60%;
  background-color:#fff;
  padding:10px;
}
.pageright .recommend h2{
  color:rgb(1,200,181)
}
/* 发布书籍 */
.mySellItem{
  border:1px rgb(222,222,222) solid;
  margin:20px 50px;
  padding:30px;
  width:60%;
}
.avatar-uploader .el-upload {
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}
.avatar-uploader .el-upload:hover {
    border-color: #409EFF;
}
.avatar-uploader-icon {
    border: 0.8px dashed rgb(189,199,216);
    font-size: 20px;
    color: #8c939d;
    width: 150px;
    height:180px;
    line-height: 178px;
    margin-left: 20px;
    text-align: center;
}
.avatar {
    width: 150px;
    height: 180px;
    display: block;
    margin-left: 20px;
}
.textarea{
  border-color:rgb(189,199,216) ;
  padding:10px
}
.sellbooks{
  margin-left: 600px;
}
</style>
