<template>
  <div class="home">
    <div>
      <!-- 顶部导航栏区域 -->
      <div class="nav">
        <p class="welcome">您好！欢迎来到西邮二手书交易网！</p>
        <div class="navigat">
          <ul>
            <li><router-link to="/personal">我的主页</router-link></li> 
            <li><router-link to="/shopcar">购物车</router-link></li>    
            <li ><router-link :to="token?'/kefu': '/login'">{{token}}</router-link></li>   
            <li><div @click="logout">退出</div></li> 
          </ul>
        </div>
      </div>
      <!-- 中间区域 -->
      <div class="oldtitle">
        <div class="h1">
          <h1>我的发布</h1>
        </div>
        <div class="navtitle">
          <ul>
            <router-link to="/home">首页</router-link>
            <router-link to="/sell">出售</router-link>
            <router-link to="/orderList">我的订单</router-link>
            <router-link to="/fabued">我的发布</router-link>
          </ul>
        </div>
      </div>
      <!-- 主页中心 -->
      <div class="shopmain">
        <div class="mainone">
          <div class="maintwo">
            <div class="myShopCar">
              <h1>我发布的书籍</h1>
            </div>
            <div class="myShops">
              <el-table :data="selltableList" stripe style="width: 100%" class="tableBox" >
                <el-table-column type="index" label="序号" width="55" align="center"></el-table-column>
                <el-table-column prop="bookName" label="书名" width="120" align="center"></el-table-column>
                <el-table-column  label="图片" width="150" align="center">
                      <template slot-scope="scope">
                        <el-image
                        style="width: 100px; height: 100px"
                        :src="'http://49.234.70.67:8081/'+scope.row.bookSrc"></el-image>
                      </template>
                </el-table-column>
                <el-table-column prop="bookIntroduction" label="简介" width="130" align="center"></el-table-column>
                <el-table-column prop="bookStyle" label="类型" width="120" align="center">
                    <template slot-scope="scope">{{scope.row.bookStyle.join('、')}}</template>
                </el-table-column>
                <el-table-column prop="bookDate" label="时间" width="120" align="center">
                  <template slot-scope="scope">{{scope.row.bookDate|dataFormat}}</template>
                </el-table-column>
                <el-table-column prop="price" label="价格" width="100" align="center">
                  <template slot-scope="scope">
                          {{scope.row.price.toFixed(2)}}
                  </template>
                </el-table-column>
                <el-table-column prop="stock" label="库存" width="100" align="center">
                  <template slot-scope="scope">{{scope.row.stock}}件</template>
                </el-table-column>
                <el-table-column label="操作" width="140px" align="center">
                <template slot-scope="scope">
                  <el-button type="primary" icon="el-icon-edit" size="mini"
                  @click="modefyBooks(scope.row.bookId)">
                    修改书籍</el-button>
                </template>
              </el-table-column>
              </el-table>
              <!-- 修改对话框 -->
              <el-dialog title="修改书籍信息" :visible.sync="dialogFormVisible">
                <el-form :model="{modefyList}">
                  <el-form-item label="书目名称" >
                    <el-input v-model="modefyList.bookName" autocomplete="off"></el-input>
                  </el-form-item>
                  <el-form-item label="书籍简介" >
                    <textarea class="textarea"  v-model="modefyList.bookIntroduction"  name id cols="51" rows="10" placeholder="(请填写书籍简介,不超过200字)"></textarea>
                  </el-form-item>
                  <el-form-item label="价格" >
                      <el-input-number v-model="modefyList.price" :precision="2" :step="0.1" :max="100000"></el-input-number>
                  </el-form-item>
                  <el-form-item label="书籍类型" >
                    <el-select v-model="modefyList.bookStyle" multiple placeholder="请选择类型">
                      <el-option
                        v-for="item in options"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                      </el-option>
                  </el-select>
                  </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                  <el-button @click="dialogFormVisible = false">取 消</el-button>
                  <el-button type="primary" @click="editBooks(modefyList.bookId)">确 定</el-button>
                </div>
              </el-dialog>
              <!-- 分页 -->
              <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="queryInfo.page"
                    :page-sizes="[3,5,10,15]"
                    :page-size="queryInfo.size"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total">
                  </el-pagination>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import footer from '../components/footer.vue'
export default {
    data() {
        return {
            token:'客服',
            total: 30,
            AllMyFabuds:{},
            selltableList:[],
            queryInfo: {
                page: 1, //页数
                size:5  //当前页数的数据个数
            },
            srcList:'',
            dialogFormVisible:false,
            modefyList:[], //点击修改获取原有的数据
            // elTableH:null,  //此处的elTableH的值应该是null   否则要经过调整窗口的大小才会生效
            options: [{
              value: '科幻',
              label: '科幻'
            }, {
              value: '悬疑',
              label: '悬疑'
            }, {
              value: '灾难',
              label: '灾难'
            }, {
              value: '爱情',
              label: '爱情'
            }, {
              value: '计算机',
              label: '计算机'
            }, {
              value: '数学',
              label: '数学'
            }, {
              value: '电子',
              label: '电子'
            }, {
              value: '历史',
              label: '历史'
            }, {
              value: '文艺',
              label: '文艺'
            }, {
              value: '网络',
              label: '网络'
            }],
        };
    },
    components:{
        "footer-box":footer
    },
    mounted(){
        // 获取我发布的
        this.getAllMyFabud()
        // this.getTbHeight()
    },
    // beforeMount(){
    //     window.addEventListener('resize',this.getTbHeight)
    // },
    methods: {
        // 获取table的值
        // getTbHeight(){
        //     this.elTableH = window.innerHeight-100
        // },
        // 获取所有我发布过的书籍
        getAllMyFabud(){
            this.$http({
            url:'http://49.234.70.67:8081/u/search/book/own',
            method: "post",
            crossdomain: true,
            data:{
                page:this.queryInfo.page,
                size:this.queryInfo.size
            },
            headers: {
                'Content-Type': 'application/json',
                'token':window.sessionStorage.getItem('token')
            },
            }).then(res => {
                // console.log(params)
                console.log(res)
                if(!res.config.headers.token){
                  this.token = '登陆/注册'
                } 
                this.selltableList = res.data.data[0]
                this.srcList = this.selltableList[0].bookSrc
                // console.log('selltableList',this.selltableList)
                if (res.data.status === -1) {
                    this.$message.error(res.data.message);
                    this.$router.push('/login')
                }
            }).catch(res=>{
                console.log('未获取到我发布过的书籍')
            })
        },
        // 修改按钮
        modefyBooks(bookId){
            this.dialogFormVisible = true
              this.$http({
              url: `http://49.234.70.67:8081/u/search/book/${bookId}`,
              method: "get",
              crossdomain: true,
              headers: {
                  'Content-Type': 'application/json',
                  'token':window.sessionStorage.getItem('token')
              },
              }).then(res => {
                console.log(res)
                if (res.data.status === 200) {
                    this.modefyList = res.data.data[0]
                }
                if (res.data.status === -1) {
                    this.$message.error(res.data.message);
                    this.$router.push('/login')
                }
              }).catch(res=>{
                console.log('error')
              })
        },
        // 修改书籍  确定按钮
        editBooks(bookId){
        this.dialogFormVisible = false
        this.$http({
              url: 'http://49.234.70.67:8081/u/update/book',
              method: "post",
              crossdomain: true,
              headers: {
                  'Content-Type': 'application/json',
                  'token':window.sessionStorage.getItem('token')
              },
              data:{
                bookId:bookId,
                bookName:this.modefyList.bookName,
                bookIntroduction:this.modefyList.bookIntroduction,
                price:this.modefyList.price,
                bookStyle:this.modefyList.bookStyle
              }
              }).then(res => {
                console.log(res)
                // if (res.data.status === 200) {
                  // 重新发送请求
                    this.getAllMyFabud()
                    this.$message.success('修改成功！');
                // }
                if (res.data.status === -1) {
                    this.$message.error(res.data.message);
                    this.$router.push('/login')
                }
              }).catch(res=>{
                console.log('error')
              })
        },
       // 数据分页
        handleSizeChange(newsize) {
          this.queryInfo.size = newsize;
          this.getAllMyFabud();
        },
        handleCurrentChange(newnum) {
          this.queryInfo.page = newnum;
          this.getAllMyFabud();
        },
        // 退出
        logout(){
         this.$http({
            url: "http://49.234.70.67:8081/u/logout",
            method: "get",
            crossdomain: true,
            headers: {
                'token':window.sessionStorage.getItem('token')
            }
            }).then(res => {
              console.log(res)
              window.sessionStorage.clear();
              this.$router.push("/login");
              if (res.data.status === -1) {
                  this.$message.error(res.data.message);
                  this.$router.push('/login')
              }
            }).catch(res=>{
              console.log('未获取到')
            })
         },
    }
};
</script>
<style  scoped>
.oldtitle {
  width: 100%;
  height: 80px;
  background-color: #fff;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.oldtitle .h1 {
  color: rgb(1, 200, 181);
}
.navtitle ul a {
  text-decoration: none;
  color: rgb(100, 100, 100);
  font-size: 18px;
  font-weight: bold;
  margin-right: 70px;
}
.navtitle ul a:hover {
  color: rgb(1, 200, 181);
}
/* 中间区域 */
.shopmain .mainone {
  background-color: rgb(245, 245, 245);
  width: 100%;
  /* border:1px red solid; */
  /* height: 500px; */
  padding: 10px;
}
.shopmain .maintwo {
  margin: 0 auto;
  width: 1000px;
  height: 40px;
  background-color: #fff;
  padding: 15px;
}
.myShopCar h1 {
  color: rgb(119, 119, 119);
}
.maintwo .myShops {
  width: 1035px;
  margin: 20px auto;
  margin-left: -17px;
  background-color: #fff;
  padding:20px;
}
.tableBox th{
  padding:0 !important;
  max-height:200px; 
}
.tableBox td{
  padding:0 !important;
  max-height: 200px; 
}
.el-pagination{
  margin:10px;
}
/* 该方法无法改变table的默认行高 */
/* .el-table__row{
  max-height:100px;
  overflow: hidden;
} */
</style>